import React from 'react';

import {useSelector, useDispatch} from 'react-redux';
import {useParams} from 'react-router-dom';
import {movieList} from '../slices/MovieSlice';
import Top from '../components/Top';
//컴포넌트 참조 제대로 확인하기
import MovieRankList from '../components/MovieRankList';
import MovieChart from '../components/MovieChart';

//로딩 컴포넌트 참조
import Loading from '../components/Loading';



const MovieRank = () => {
    const {rt, rtmsg, item, loading} = useSelector((state)=>state.movieRank);
    const dispatch = useDispatch();
    let {targetDt} = useParams() 

    React.useEffect(()=>{
        dispatch(movieList(targetDt));
    },[dispatch, targetDt])
    
    return (
        <div>
            <Top targetDt={targetDt}/>
            {loading && (
                
                <Loading />
            )}
            {rt !== 200 ? (
                <div>
                    <h2>{rt} {rtmsg}</h2>
                </div>
            ) : (
                <div>
                    <MovieChart targetDt={targetDt} chartData={item.chartData} />
            <MovieRankList boxOfficeResult={item.boxOfficeResult} />
                </div>
            )}
            
            
        </div>
    );
};

export default MovieRank;