import React from 'react';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
  } from 'chart.js';
  import { Bar } from 'react-chartjs-2';
  
  ChartJS.register(
      CategoryScale,
      LinearScale,
      BarElement,
      Title,
      Tooltip,
      Legend
    );
const MovieChart = ({targetDt, chartData}) => {
    React.useEffect(()=>{

    },[targetDt, chartData])

    const options = {
        responsive: true,
        plugins: {
          legend: {
            position: 'bottom'
          },
          title: {
            display: true,
            text: targetDt + '관람객 수 순위',
          },
        },
      };
      const data = {
        labels : chartData.movieNm,
        datasets: [
          {
            label: '관람객 수',
            backgroundColor: 'rgba(255, 99, 132, 0.5)',
            borderColor: 'rgb(255, 99, 132)',
            borderWidth : 1,
            data : chartData.audiCnt
          },
         
        ],
      };
    return (
        <div>
            <Bar 
            data={data}
            options={options}
        />
        </div>
    );
};
MovieChart.defaultProps = {
    movieNm : [],
    audiCnt : []
}
export default MovieChart;