import {createSlice, createAsyncThunk} from '@reduxjs/toolkit';
import axios from 'axios';
import dayjs from 'dayjs';
export const movieList = createAsyncThunk('movieList_get', async(payload, {rejectedWithValue})=>{
    let result = null;
    let date = new Date();

    date = dayjs().add(-1, 'd').format('YYYYMMDD');
    const targetDt = payload.replaceAll('-', '');
    
    if(payload === undefined){
        payload = date;
    }
    
    console.log(date);
    
    try {
        if(parseInt(targetDt) > parseInt(date)){
            alert('조회가능한 날짜는 하루 전 까지입니다.');
            const err = new Error();
            err.response = {status: 400, message:'조회가능한 날짜는 하루 전 까지입니다'}
            throw err;
        }
        
        result = await axios.get("http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json", {
            params : {
                key : 'da4074f8387339e2078a14dc346b4759',
                targetDt : targetDt
            }
        })
        
    }catch(err){
        result = rejectedWithValue(err.response);
    }
    return result;
});

export const movieRankSlice = createSlice({
    name : 'movieRank',
    initialState : {
        rt : null,
        rtmsg : null,
        item : [],
        loading : false
    },

    reducers : {},

    extraReducers : {
        [movieList.pending] : (state, {payload})=>{
            return {
                ...state,
                loading : true,
            }
        },
        [movieList.fulfilled] : (state, {payload})=>{

            const chartData = {movieNm : [], audiCnt : []}
            payload.data.boxOfficeResult.dailyBoxOfficeList.forEach((v, i)=>{
                chartData.movieNm[i] = v.movieNm
                chartData.audiCnt[i] = v.audiCnt
            })
            payload.data.chartData = chartData

            return {
                ...state,
                rt : payload.status,
                rtmsg : payload.statusText,
                item : payload.data,
                loading : false
            }
        },
        [movieList.rejected] : (state, {payload})=>{
            return {
                ...state,
                rt : payload?.status ? payload.status : 500,
                rtmsg : payload?.statusText ? payload.statusText : 'SeverError',
                item : [],
                loading : false
            }
        }
    },
});

export default movieRankSlice.reducer